.body {
    background-color: rgba(46, 61, 87, 0.3);
    width: 50%;
    height: 50%;
    border: 1px solid blue;
    margin: 0px auto;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 10px;

}

.count {
    border: 1px solid blue;
    width: 25%;
    height: 25%;
    text-align: center;
    padding: 10px;
}
.countDisabled {
    color: red;
    border: 1px solid blue;
    width: 25%;
    height: 25%;
    text-align: center;
    padding: 10px;
}

.buttons {
    border: 1px solid blue;
    text-align: center;
    display: flex;
    padding: 10px;
    margin: 10px;
    gap: 10px;
}

.inc {
    color: red;
}

.reset {
    text-color: grey;
}
.error {
    background-color: red;
}
